@tailwind base;
@tailwind components;
@tailwind utilities;


/* üåê Fuente global */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap');

/* üé® Estilos base */
html {
  scroll-behavior: smooth;
  background-color: #000;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #0f0f1a;
  color: white;
  overflow-x: hidden;
}

/* ‚ú® T√≠tulos */
h1, h2, h3, h4 {
  letter-spacing: 0.5px;
}

h2 {
  @apply text-4xl font-bold mb-6;
}

/* üì± Secciones generales */
section {
  padding-top: 6rem;
  padding-bottom: 6rem;
  transition: all 0.6s ease-in-out;
  scroll-margin-top: 100px; /* compensa el navbar fijo */
}

/* üéÜ Efecto glassmorphism para nav y secciones con fondos transl√∫cidos */
.glass {
  background: rgba(17, 17, 23, 0.6);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 25px rgba(236, 72, 153, 0.15);
}

/* üåà Efectos de brillo ne√≥n */
.neon-text {
  background: linear-gradient(90deg, #db2777, #7e22ce, #06b6d4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.neon-border {
  box-shadow: 0 0 20px rgba(236, 72, 153, 0.6);
}

/* üí® Animaciones */
.fade-in {
  animation: fadeIn 1.2s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(15px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* üß≠ Scrollbar personalizada */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #7e22ce, #db2777);
  border-radius: 8px;
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
header, nav, .glass {
  all: revert-layer;
}


/* ‚úÖ Asegura que el body sea transparente y permita ver el canvas */
html, #root {
  height: 100%;
  background: transparent !important;
  position: relative;
  overflow-x: hidden;
}
/* üåä Fondo general con tono verde agua suave */
body {
  background: radial-gradient(circle at center, #0f172a 0%, #064e3b 100%);
  background-attachment: fixed;
  color: white;
  overflow-x: hidden;
}
